import React, { useState, useEffect } from 'react';
import Layout from './MainLayout';
import Button from '@material-ui/core/Button';
import Card from './Card';
import { getFilteredProducts, shareProduct } from './apiCore';
import AddIcon from '@material-ui/icons/Add';

const Main = () => {
  const [error, setError] = useState(false);
  const [limit, setLimit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [filteredResults, setFilteredResults] = useState([]);

  const loadFilteredResults = () => {
    // console.log(newFilters);
    getFilteredProducts(skip, limit).then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        setFilteredResults(data.data);
        setSize(data.size);
        setSkip(0);
      }
    });
  };
  const loadMore = () => {
    let toSkip = skip + limit;
    // console.log(newFilters);
    getFilteredProducts(toSkip, limit).then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        setFilteredResults([...filteredResults, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      }
    });
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <AddIcon />
        </Button>
      )
    );
  };
  useEffect(() => {
    loadFilteredResults(skip, limit);
  }, []);

  const shopItemClicked = (_id, share) => {
    shareProduct(_id, share).then((data) => {
      if (!data || data.error) {
        setError(data.error);
      } else {
        const idx = filteredResults.findIndex(itm => itm._id === _id)
        if (idx < 0) return
        filteredResults[idx].shared = data.shared;
        setFilteredResults([...filteredResults]);
      }
    });
  }

  return (
    <Layout
      title='Shop page'
      description='Search and find books'
      className='container-fluid'
    >
      <div>
        <div className='d-flex flex-wrap justify-content-between'>
          {filteredResults.map((product, i) => (
            <div key={i} className={'shop-item ' + (product.shared ? 'selected' : '' )} onClick={() => shopItemClicked(product._id, 1 - product.shared)}>
              <Card product={product}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
    </Layout>
  );
};

export default Main;
  